/* This file is for your main application css. */
/* General style */
h1 {
  font-size: 4.0rem;
  line-height: 1.25; }

h2 {
  font-size: 2.8rem;
  line-height: 1.3; }

h3 {
  font-size: 2.2rem;
  letter-spacing: -.08rem;
  line-height: 1.35; }

h4 {
  font-size: 1.8rem;
  letter-spacing: -.05rem;
  line-height: 1.5; }

h5 {
  font-size: 1.6rem;
  letter-spacing: 0;
  line-height: 1.4; }

h6 {
  font-size: 1.4rem;
  letter-spacing: 0;
  line-height: 1.2; }

p {
  font-size: 20px; }

pre {
  padding: 1em; }

.container {
  margin: 0 auto;
  max-width: 80.0rem;
  padding-top: 10%;
  padding-bottom: 10%;
  position: relative;
  width: 100%;
  text-align: center;
  font-size: 20px; }

.data-container {
  margin: 0 auto;
  max-width: 95.0rem;
  padding-top: 10%;
  padding-bottom: 10%;
  width: 100%;
  text-align: center;
  font-size: 20px; }

code {
  background-color: black;
  font-size: 20px;
  white-space: break-spaces;
  color: #aef77d; }

/* #e300ff,#b0ff00,#00ffd2,#fdff00,#ff00f4 */
.container p {
  color: #fffce1; }

.container h2 {
  margin-top: 55px;
  color: #fffce1; }

.intro {
  text-align: center; }

.intro h1 {
  color: #fa26a0; }

.info p {
  padding-top: 45px; }

.more-info {
  text-align: left;
  padding-top: 140px; }

.data-api p {
  padding-top: 45px; }

.uuid {
  color: #00ffe7; }

.textdb {
  color: #fa26a0; }

a {
  border-bottom: 1px solid #fffce1; }

select {
  width: auto; }

button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
  cursor: pointer; }

button.selected {
  cursor: pointer;
  border-bottom: 1px solid white; }

.green {
  color: #aef77d; }

.green a {
  color: #aef77d; }

.edit {
  margin-top: 15px;
  padding: 0 10px;
  display: inline-block; }

.edit:hover {
  cursor: pointer; }

.data textarea {
  color: #fffce1;
  text-align: center; }

.data {
  font-size: 20px;
  margin-top: 25px;
  min-width: 50%;
  min-height: 600px; }

.data textarea.right {
  text-align: right; }

.data textarea.center {
  text-align: center; }

.data textarea.left {
  text-align: left; }

textarea {
  font-size: 20px;
  min-height: 600px; }

textarea[readonly] {
  border: 1px solid white; }

/* Headers */
header {
  width: 100%;
  background: #fdfdfd;
  border-bottom: 1px solid #eaeaea;
  margin-bottom: 2rem; }

/* This file is for your main application css. */
/* Alerts and form errors */
.alert {
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 4px; }

.alert-info {
  color: #31708f;
  background-color: #d9edf7;
  border-color: #bce8f1; }

.alert-warning {
  color: #8a6d3b;
  background-color: #fcf8e3;
  border-color: #faebcc; }

.alert-danger {
  color: #a94442;
  background-color: #f2dede;
  border-color: #ebccd1; }

.alert p {
  margin-bottom: 0; }

.alert:empty {
  display: none; }
